}

.l-main {
  height: 200px;
  margin-top: 58px;
}

@media screen and (min-width: 768px) {
  .l-main {
    margin-top: 98.26667px;
  }
}

.l-main__breadcrumb {
  position: relative;
  z-index: 90;
  background: #ffb41d;
}

.c-logo {
  width: 100%;
}

.c-logo__image {
  width: 100%;
  margin: 0;
  padding: 0;
  display: inline-block;
  vertical-align: top;
  line-height: 1;
  fill: currentColor;
}

.p-articleShare {
  display: flex;
  justify-content: space-around;
}

@media screen and (min-width: 768px) {
  .p-articleShare {
    display: block;
    position: sticky;
    top: 148.26667px;
  }
}

.p-articleShare__button {
  width: 40px;
  height: 40px;
  margin-bottom: 8px;
  padding: 8px;
  display: block;
  color: #202020;
  transition-duration: .2s;
}

.p-articleShare__button--twitter:hover {
  color: #1da1f2;
}

.p-articleShare__button--facebook:hover {
  color: #1877f2;
}

.p-articleShare__button--hatena:hover {
  color: #008fde;
}

.p-articleShare__button--line:hover {
  color: #00c300;
}

.p-articleShare__icon {
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.p-breadcrumb {
  max-width: 1200px;
  margin: auto;
  padding: 6px 16px 4px;
  overflow: hidden;
  white-space: nowrap;
  list-style: none;
  text-overflow: ellipsis;
  font-size: 0.72727rem;
}

@media screen and (min-width: 768px) {
  .p-breadcrumb {
    padding: 6px 24px 4px;
    font-size: 0.8rem;
  }
}

.p-breadcrumb__item {
  display: inline;
}

.p-breadcrumb__item:first-child {
  display: none;
}

@media screen and (min-width: 768px) {
  .p-breadcrumb__item:first-child {
    display: inline;
  }
}

.p-breadcrumb__item:not(:last-child)::after {
  content: "\f105";
  margin: 0 2px 0 4px;
  font-family: "Font Awesome 5 Free", "fontawesome", sans-serif;
  font-size: 0.66667rem;
}

@media screen and (min-width: 768px) {
  .p-breadcrumb__item:not(:last-child)::after {
    margin: 0 4px 0 6px;
    font-size: 0.72727rem;
  }
}

.p-breadcrumb__item:hover {
  color: #3c3c3c;
}

.p-globalNav__hamburger {
  width: 20px;
  height: 24px;
  position: relative;
  z-index: 110;
  cursor: pointer;
}

.p-globalNav__hamburger span {
  width: 100%;
  height: 2px;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #202020;
  transition-duration: .2s;
}

.p-globalNav__hamburger span::before, .p-globalNav__hamburger span::after {
  content: "";
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #202020;
  transition-duration: .2s;
}

.p-globalNav__hamburger span::before {
  transform: translateY(-6px);
}

.p-globalNav__hamburger span::after {
  transform: translateY(6px);
}

.p-globalNav__hamburger span.is-view {
  background: transparent;
}

.p-globalNav__hamburger span.is-view::before {
  transform: translateY(0) rotateZ(45deg);
}

.p-globalNav__hamburger span.is-view::after {
  transform: translateY(0) rotateZ(-45deg);
}

@media screen and (min-width: 768px) {
  .p-globalNav__hamburger {
    display: none;
  }
}

.p-globalNav__container {
  max-height: 80vh;
  padding: 24px 0 32px;
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  list-style: none;
  position: absolute;
  left: 0;
  top: 60px;
  z-index: 120;
  background: #fff;
  border-bottom: solid 2px #e0e0e0;
  pointer-events: none;
  opacity: 0;
  transform: translateY(-16px);
  transition-duration: .2s;
}

@media screen and (min-width: 768px) {
  .p-globalNav__container {
    padding: 0;
    position: initial;
    overflow-y: initial;
    border-bottom: none;
    pointer-events: all;
    opacity: 1;
    transform: translateY(0);
  }
}

.p-globalNav__container.is-view {
  pointer-events: all;
  opacity: 1;
  transform: translateY(0);
}

.p-globalNav__item {
  width: 100%;
  border-bottom: solid 1px transparent;
  transition-duration: .2s;
}

@media screen and (min-width: 768px) {
  .p-globalNav__item {
    width: auto;
  }
}

@media screen and (min-width: 768px) {
  .p-globalNav__item.is-current, .p-globalNav__item:hover {
    border-bottom: solid 1px #ffb41d;
  }
}

.p-globalNavItem__container {
  padding: 8px 16px;
  display: block;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.p-globalNavItem__container:hover {
  color: #3c3c3c;
  background: #e0e0e0;
}

@media screen and (min-width: 768px) {
  .p-globalNavItem__container:hover {
