  color: #535353;
  background: #f0f0f0;
  font-size: 0.88889rem;
  transition-duration: .2s;
}

@media screen and (min-width: 768px) {
  .p-globalNavItem__accordion {
    position: absolute;
    left: 0;
    top: 78.26667px;
    font-size: 1rem;
  }
}

.p-globalNavItem__accordion.is-view {
